import React from 'react';

import { Color } from '../utils/variables';

import { Column, Row } from './Layout';
import { Label } from './Typography';
import { Rate } from './Progress';

import styled from 'styled-components';

const Title = styled(Row)`
  position: absolute;
  bottom: 0;
  left: 10px;
  right: 10px;
  background-color: rgba(255, 255, 255, 0.8);
  padding: 10px;
  min-height: 50px;
  align-items: center;
  margin-bottom: 10px;
`;

const ThumbnailWrapper = styled(Column)`
  ${(props) =>
    !props.details &&
    `
      background-color: white;
      transition: all 0.2s ease;
      
      &:hover {
        background-color: ${Color('gray', '0.3')};
        transform: scale(1.05);
        box-shadow: 0px 10px 15px rgba(0, 2, 0, 0.25);
        
        ${Title} {
          background-color: rgba(255, 255, 255, 0.6);
        }
      }
  `};
`;

export const Image = ({ movie }) => (
  <Column fit center middle>
    {movie.backdrop_path ? (
      <img
        width="220px"
        height="330px"
        alt={movie.title || movie.original_title || movie.name}
        src={`https://www.themoviedb.org/t/p/w220_and_h330_face/${movie.backdrop_path}`}
      />
    ) : (
      <Column randomBkg fade="0.6">
        <img
          width="220px"
          height="330px"
          alt={movie.title || movie.original_title || movie.name}
          src={
            'data:image/svg+xml;base64,'
          }
        />
      </Column>
    )}
  </Column>
);

export const Thumbnail = ({ movie, details }) => (
  <ThumbnailWrapper
    center
    marginAll
    className={details ? '' : 'pointer'}
    radius="8"
    paddingAll="10"
    relative
    details={details}
  >
    <Column radius="8">
      <Image movie={movie} />
    </Column>

    {!details && (
      <Column>
        <Title center>
          <Label bold color="gray" fade="0.9" center>
            {movie.title || movie.original_title || movie.name}
          </Label>
        </Title>
        {(movie.vote_average || movie.vote_average === 0) && (
          <Rate autoColor value={movie.vote_average} />
        )}
      </Column>
    )}
  </ThumbnailWrapper>
);
